import React, { Component } from 'react';


class Quotes extends Component {
  render() {
    return (
      <div className={this.props.showQuotes? 'quote-container' : "quote-container hidden"}>
        <p>{this.props.quote}</p>
      </div>
    );
  }
}

export default Quotes;



