import React, { Component } from 'react';

class Forecast extends Component {

  constructor() {
    super()


    this.state = {
      showForecast: false
    }

  this.makeForecast = this.makeForecast.bind(this)
  }

  handleForecastToggle(event) {
      this.setState({
          showForecast: !this.state.showApp
      })
  }

  makeForecast(arr){
   return arr.slice(1).map((day, i) =>{
        return (
          <div className='forecast' key={i}>
            <h4>{day.dt_txt}</h4>
            <p>{day.main.temp}&deg;</p>
            <p>{day.weather ? day.weather[0].main : "Nothing to report"}</p>
          </div>
        )})

  }

  render() {
    return (
      <div>
        <div className={this.state.showForecast ? 'forecast-container' : "forecast-container hidden"}>
          {this.makeForecast(this.props.weather)}
        </div>
      </div>
    );
  }

}

export default Forecast;



