import React, { Component } from 'react';
import './App.css';
import SearchForm from './components/search'
import Weather from './components/weather'
import Quotes from './components/quotes'
import Forecast from './components/forecast'

const apiKey = 'appid=3980d89393d880cd195910d54ea88a9c'

class App extends Component {
    constructor() {
        super()

        this.state = {
            forcastData: {},
            showApp: false,
            showQuotes: false,
            zipCode: 0,
            description: null,
            quotes: {
                quotes: []
            },
            randomInt: null,
            weatherArray: []
        }

        this.getRandomInt = this.getRandomInt.bind(this)
    }



    handleOpenClick(event) {
        this.setState({
            showApp: !this.state.showApp
        })
    }

    handleChange(e) {
        this.setState({
            zipCode: e.target.value
        })
    }

    getQuote(e) {
        e.preventDefault();
        this.getRandomInt(this.state.quotes.quotes.length)
        fetch('myData.json')
            .then(resp => resp.json())
            .then(data => {
                this.setState({
                    quotes: data,
                    showQuotes: !this.state.showQuotes
                });
            })
    }




    getWeather(e) {
        e.preventDefault();
        const weatherArray = []
        const zip = this.state.zipCode
        if(/^[0-9]{5}(?:-[0-9]{4})?$/.test(zip)){
          fetch(`http://api.openweathermap.org/data/2.5/weather?zip=${zip}&units=imperial&${apiKey}`)
              .then(resp => resp.json())
              .then(data => {
                  weatherArray.push(data)
                  return data
              })
              .then(resp => {
                  this.setState({
                      weatherArray: weatherArray,
                      description: resp.weather
                  })
                  return this.state.weatherArray.length
              })
              .then(resp => {
                  if (resp < 6) {
                      fetch(`http://api.openweathermap.org/data/2.5/forecast?zip=${zip},us&units=imperial&${apiKey}`)
                          .then(resp => resp.json())
                          .then(resp => resp.list)
                          .then(data => (data.filter((item) => item.dt_txt.includes('12:00:00'))))
                          .then(filteredData => {
                              filteredData.forEach(day => weatherArray.push(day))
                              console.log(this.state.weatherArray)
                              return weatherArray
                          })
                          .then(weatherArray => this.setState({
                              weatherArray: weatherArray
                          }))
                  }
              })
          } else {
            alert('Not valid ZipCode')
          }
    }

    getRandomInt(max) {
        this.setState({
            randomInt: Math.floor(Math.random() * max)
        })
    }


  render() {
    return (
      <div className="App">
        <div className="brochure">
          <div className="about-container">
          <h1>Weather App</h1>
          <h3>About</h3>
          <p>Without weather applications, half of all Americans would have nothing to talk about. Luckily, I have created an answer to the age-old question &quot;How&apos;s the weather?&quot;. With a touch of sarcasm and humor, this app will not only let you know what its like in Rancho Cucamonga but also crack a weather-related joke. Anyone can use it and hopefully it brightens your day.</p>
          <button onClick={this.handleOpenClick.bind(this)}> Open the App </button>
          </div>
        </div>
          <main className={this.state.showApp? 'search-container' : "search-container hidden"}>
          <SearchForm handleChange={this.handleChange.bind(this)} getWeather={this.getWeather.bind(this)} showApp={this.state.showApp}/>
          <Weather className="weather" weatherData={this.state.weatherArray[0] && this.state.weatherArray[0]} description={this.state.description && this.state.description} getQuote={this.getQuote.bind(this)} mainData={this.state.weatherArray[0] && this.state.weatherArray[0].main} forecast={this.state.weatherArray} showApp={this.state.showApp}/>
          <Quotes quote={this.state.quotes.quotes[this.state.randomInt]} showQuotes={this.state.showQuotes}/>
          <Forecast weather={this.state.weatherArray} />
        </main>
      </div>
    );
  }
}

export default App;



