import React,  { Component } from 'react';

class SearchForm extends  Component{
  constructor(){
    super()
    this.state = {
      zipCode: 0,
      description: []
    }
  }



  render() {
    return (
      <div className={this.props.showApp ? 'search-container' : "search-container hidden"}>
        <form id="zipCode-input" onSubmit={this.props.getWeather} >
          <label>Enter your Zip Code: </label>
          <input id="zipCode" type="text" onChange={this.props.handleChange}></input>
          <input id="submit" type="submit" value="Submit" />
        </form>
      </div>
    );
  }

}

export default SearchForm;



